<h1 id="用户标签" tabindex="-1"><a class="header-anchor" href="#用户标签" aria-hidden="true">#</a> 用户标签</h1>
<p>用户标签的使用就非常简单了，基本的增删改查。</p>
<h2 id="获取实例" tabindex="-1"><a class="header-anchor" href="#获取实例" aria-hidden="true">#</a> 获取实例</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token php language-php"><span class="token delimiter important">&lt;?php</span>
<span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Foundation<span class="token punctuation">\</span>Application</span><span class="token punctuation">;</span>

<span class="token comment">// ...</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Application</span><span class="token punctuation">(</span><span class="token variable">$options</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$tag</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">user_tag</span><span class="token punctuation">;</span> <span class="token comment">// $user['user_tag']</span>
</span></code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h2 id="api" tabindex="-1"><a class="header-anchor" href="#api" aria-hidden="true">#</a> API</h2>
<h3 id="获取所有标签" tabindex="-1"><a class="header-anchor" href="#获取所有标签" aria-hidden="true">#</a> 获取所有标签</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$tag</span><span class="token operator">-></span><span class="token function">lists</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$tags</span> <span class="token operator">=</span> <span class="token variable">$tag</span><span class="token operator">-></span><span class="token function">lists</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// {</span>
<span class="token comment">//     "tags": [</span>
<span class="token comment">//         {</span>
<span class="token comment">//             "id": 0,</span>
<span class="token comment">//             "name": "标签1",</span>
<span class="token comment">//             "count": 72596</span>
<span class="token comment">//         },</span>
<span class="token comment">//         {</span>
<span class="token comment">//             "id": 1,</span>
<span class="token comment">//             "name": "标签2",</span>
<span class="token comment">//             "count": 36</span>
<span class="token comment">//         },</span>
<span class="token comment">//         ...</span>
<span class="token comment">//     ]</span>
<span class="token comment">// }</span>

<span class="token function">var_dump</span><span class="token punctuation">(</span><span class="token variable">$tags</span><span class="token operator">-></span><span class="token property">tags</span><span class="token punctuation">[</span><span class="token number">0</span><span class="token punctuation">]</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'name'</span><span class="token punctuation">]</span><span class="token punctuation">)</span> <span class="token comment">// “标签1”</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br></div></div><h3 id="创建标签" tabindex="-1"><a class="header-anchor" href="#创建标签" aria-hidden="true">#</a> 创建标签</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$tag</span><span class="token operator">-></span><span class="token function">create</span><span class="token punctuation">(</span><span class="token variable">$name</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$tag</span><span class="token operator">-></span><span class="token function">create</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'测试标签'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="修改标签信息" tabindex="-1"><a class="header-anchor" href="#修改标签信息" aria-hidden="true">#</a> 修改标签信息</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$tag</span><span class="token operator">-></span><span class="token function">update</span><span class="token punctuation">(</span><span class="token variable">$tagId</span><span class="token punctuation">,</span> <span class="token variable">$name</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$tag</span><span class="token operator">-></span><span class="token function">update</span><span class="token punctuation">(</span><span class="token number">12</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"新的名称"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="删除标签" tabindex="-1"><a class="header-anchor" href="#删除标签" aria-hidden="true">#</a> 删除标签</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$tag</span><span class="token operator">-></span><span class="token function">delete</span><span class="token punctuation">(</span><span class="token variable">$tagId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$tag</span><span class="token operator">-></span><span class="token function">delete</span><span class="token punctuation">(</span><span class="token variable">$tagId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="获取指定-openid-用户身上的标签" tabindex="-1"><a class="header-anchor" href="#获取指定-openid-用户身上的标签" aria-hidden="true">#</a> 获取指定 openid 用户身上的标签</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$userTags</span> <span class="token operator">=</span> <span class="token variable">$tag</span><span class="token operator">-></span><span class="token function">userTags</span><span class="token punctuation">(</span><span class="token variable">$openId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">//</span>
<span class="token comment">// {</span>
<span class="token comment">//     "tagid_list":["标签1","标签2"]</span>
<span class="token comment">// }</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h3 id="获取标签下粉丝列表" tabindex="-1"><a class="header-anchor" href="#获取标签下粉丝列表" aria-hidden="true">#</a> 获取标签下粉丝列表</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$tag</span><span class="token operator">-></span><span class="token function">usersOfTag</span><span class="token punctuation">(</span><span class="token variable">$tagId</span><span class="token punctuation">,</span> <span class="token variable">$nextOpenId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">// $nextOpenId：第一个拉取的OPENID，不填默认从头开始拉取</span>

<span class="token comment">// {</span>
<span class="token comment">//   "count":2,//这次获取的粉丝数量</span>
<span class="token comment">//   "data":{//粉丝列表</span>
<span class="token comment">//      "openid":[</span>
<span class="token comment">//          "ocYxcuAEy30bX0NXmGn4ypqx3tI0",</span>
<span class="token comment">//          "ocYxcuBt0mRugKZ7tGAHPnUaOW7Y"</span>
<span class="token comment">//      ]</span>
<span class="token comment">//   },</span>
<span class="token comment">//   "next_openid":"ocYxcuBt0mRugKZ7tGAHPnUaOW7Y"//拉取列表最后一个用户的openid</span>
<span class="token comment">// }</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><h3 id="批量为用户打标签" tabindex="-1"><a class="header-anchor" href="#批量为用户打标签" aria-hidden="true">#</a> 批量为用户打标签</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$openIds</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token variable">$openId1</span><span class="token punctuation">,</span> <span class="token variable">$openId2</span><span class="token punctuation">,</span> <span class="token operator">...</span><span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$tag</span><span class="token operator">-></span><span class="token function">batchTagUsers</span><span class="token punctuation">(</span><span class="token variable">$openIds</span><span class="token punctuation">,</span> <span class="token variable">$tagId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h3 id="批量为用户取消标签" tabindex="-1"><a class="header-anchor" href="#批量为用户取消标签" aria-hidden="true">#</a> 批量为用户取消标签</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$openIds</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token variable">$openId1</span><span class="token punctuation">,</span> <span class="token variable">$openId2</span><span class="token punctuation">,</span> <span class="token operator">...</span><span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$tag</span><span class="token operator">-></span><span class="token function">batchUntagUsers</span><span class="token punctuation">(</span><span class="token variable">$openIds</span><span class="token punctuation">,</span> <span class="token variable">$tagId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>关于用户管理请参考微信官方文档：http://mp.weixin.qq.com/wiki/ <code>用户管理</code> 章节。</p>
